<template>
  <div>
    <el-button type="mini" @click="refresh()"></el-button>
    <el-table :data="tableData" style="width: 100%" height="600">
      <el-table-column prop="Ldate" label="" sortable width="180">
      </el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope="scope">
          <el-image
            style="width: 100px; height: 100px"
            :src="tableData[scope.$index].Lurl"
            :preview-src-list="tableData[scope.$index].Lurl"
          >
          </el-image>
        </template>
      </el-table-column>
      <el-table-column prop="Lname" label="" width="180">
      </el-table-column>
      <el-table-column
        prop="Linfomation"
        label=""
        :formatter="formatter"
      >
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="success" @click="open(scope.$index)"
            ></el-button
          >
          <el-button
            v-if="stateis(scope.$index)"
            type="danger"
            icon="el-icon-delete"
            circle
            @click="changeit(scope.$index, tableData)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<style>
.image {
  width: 100px;
  height: 100px;
}
</style>
<script>
export default {
  data() {
    return {
      src:
        "http://img.whitelion233.top/images/2020/03/25/logoa104e2210c1ef437.png",
      tableData: []
    };
  },
  methods: {
    open(index) {
      //
      this.$alert(this.tableData[index].Lcontact, "", {
        confirmButtonText: ""
      });
    },
    stateis(index) {
      //
      if (this.tableData[index].Lid == localStorage.ID) return true;
      else return false;
    },
    changeit(index, rows) {
      //
      this.$http
        .post("/api/loststate", this.tableData[index])
        .then(res => {
          if (res.data) {
            this.$notify({
              title: "",
              message: "",
              type: "success"
            });
            rows.splice(index, 1);
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    refresh() {
      //
      this.$http
        .post("/api/lost", this.tableData)
        .then(res => {
          if (res.data) {
            var obj = {};
            for (var i = 0; i < res.data.data.length; i++) {
              res.data.data[i].Ldate = res.data.data[i].Ldate.slice(0, 10);
              obj[i] = res.data.data[i];
              if (this.tableData[i].id != res.data.data[i].id) {
                this.tableData.push(obj[i]);
                console.log("");
              } else console.log("");
            }

            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  },
  created: function() {
    //
    this.$http
      .post("/api/lost", this.tableData)
      .then(res => {
        if (res.data) {
          var obj = {};
          for (var i = 0; i < res.data.data.length; i++) {
            if (res.data.data[i].Lstate == 1) {
              if (this.tableData[i] != res.data.data[i]) {
                res.data.data[i].Ldate = res.data.data[i].Ldate.slice(0, 10);
                obj[i] = res.data.data[i];
                this.tableData.push(obj[i]);
              }
            }
          }
          console.log("");
        } else {
          console.log("");
          this.$notify({
            message: "",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  }
};
</script>
