<template>
  <div>
    <!--  -->
    <el-card>
      <div>
        <el-row :gutter="0">
          <el-col :span="8">
            <!--  -->
            <el-input
              placeholder=""
              v-model="queryInfo.query"
              clearable
              @clear="Refresh()"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="Search()"
              ></el-button>
            </el-input>
          </el-col>
          <el-col :span="16"> </el-col>
        </el-row>

        <!--  -->
        <el-table :data="tableData" border stripe>
          <el-table-column type="index"></el-table-column>
          <el-table-column label="" prop="Pregion"></el-table-column>
          <!-- 
          <el-table-column label="" prop=""></el-table-column
          <el-table-column label="" prop=""></el-table-column
           -->
          <el-table-column label="" prop="Pdate"></el-table-column>
          <el-table-column label="" prop="Premark"></el-table-column>
          <el-table-column label="" prop="Pdelivery"> </el-table-column>
          <el-table-column label="" prop="Purl"> </el-table-column>
          <el-table-column label="" width="150">
            <template slot-scope="scope">
              <!--  -->
              <el-tooltip
                effect="dark"
                content=""
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="showEditDialog(scope.row.id)"
                ></el-button>
              </el-tooltip>
              <!--  -->
              <el-tooltip
                effect="dark"
                content=""
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="deleteData(scope.row.id)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="PAGINATION.pagenum"
          :page-sizes="[5, 10, 100]"
          :page-size="PAGINATION.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="TotalNumberOfPages"
        >
        </el-pagination>

        <!--  -->
        <el-dialog
          title=""
          :visible.sync="editDialogVisible"
          width="50%"
          @close="addDialogClosed"
        >
          <!--  -->
          <span>
            <el-form
              ref="form"
              :model="GtableData"
              :rules="rules"
              label-width="100px"
            >
              <el-form-item label="" prop="Pregion">
                <el-select
                  v-model="GtableData.Pregion"
                  placeholder=""
                >
                  <el-option label="" value="A"></el-option>
                  <el-option label="" value="B"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="Pdate">
                <el-col :span="11">
                  <el-form-item prop="Pdate">
                    <el-date-picker
                      v-model="GtableData.Pdate"
                      type="datetime"
                      placeholder=""
                    >
                    </el-date-picker>
                  </el-form-item>
                </el-col>
              </el-form-item>
              <el-form-item label="">
                <el-input
                  type="textarea"
                  v-model="GtableData.Premark"
                ></el-input>
              </el-form-item>
            </el-form>
          </span>
          <!--  -->
          <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="Change_2()"> </el-button>
          </span>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      //
      GtableData: [],

      //
      queryInfo: {
        query: ""
      },

      //
      PAGINATION: {
        //
        pagenum: 1,
        //
        pagesize: 5
      },

      //
      TotalNumberOfPages: 0,

      //----
      value: true,
      //id
      id_id: "",
      //&&
      editDialogVisible: false,
      rules: {
        Pregion: [
          { required: true, message: "", trigger: "change" }
        ],
        Pdate: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      }
    };
  },
  created() {
    this.$http
      .get("/api/articles/makeAnAppointment/AppointmentPrint", this.tableData)
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          this.TotalNumberOfPages = res.data.data.length;

          this.tableData = [];

          for (var i = 0; i < res.data.data.length; i++) {
            res.data.data[i].Pdate = res.data.data[i].Pdate.slice(0, 10);
            if (res.data.data[i].Pdelivery == 1) {
              res.data.data[i].Pdelivery = "";
            } else res.data.data[i].Pdelivery = "";
            this.tableData.push(
              res.data.data[
                this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
              ]
            );
          }
          console.log(this.tableData);
          console.log("");
        } else {
          console.log("");
          this.$notify({
            message: "",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  methods: {
    // ---- 
    handleSizeChange(newSize) {
      this.PAGINATION.pagesize = newSize;
      this.Refresh();
    },
    //  
    handleCurrentChange(newPage) {
      this.PAGINATION.pagenum = newPage;
      this.Refresh();
    },
    // switch 
    userStateChanged() {},
    //
    showEditDialog(id) {
      this.editDialogVisible = true;
      //
      this.Change(id);
    },
    //
    Refresh() {
      this.$http
        .get("/api/articles/makeAnAppointment/AppointmentPrint", this.tableData)
        .then(res => {
          console.log(res.data.data.length);
          if (res.data) {
            this.tableData = [];

            var j = this.PAGINATION.pagesize;
            if (
              this.PAGINATION.pagenum * this.PAGINATION.pagesize >
              res.data.data.length
            ) {
              j = res.data.data.length % this.PAGINATION.pagesize;
            }
            for (var i = 0; i < j; i++) {
              this.tableData.push(
                res.data.data[
                  this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
                ]
              );
            }

            this.TotalNumberOfPages = res.data.data.length;

            console.log(this.tableData);
            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    deleteData(ID) {
      this.$http
        .delete("/api/articles/makeAnAppointment/AppointmentPrint/" + ID)
        .then(res => {
          if (res.data) {
            console.log("");
            this.Refresh();
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    Change(id) {
      this.$http
        .get("/api/articles/makeAnAppointment/AppointmentPrint")
        .then(res => {
          if (res.data) {
            for (var i = 0; i < res.data.data.length; i++) {
              if (res.data.data[i].id == id) {
                this.GtableData = res.data.data[i];
                this.id_id = id;
              }
            }
            console.log(this.GtableData);
            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    Change_2() {
      this.$http
        .post(
          "/api/articles/makeAnAppointment/AppointmentPrint/" + this.id_id,
          this.GtableData
        )
        .then(res => {
          if (res.data) {
            console.log("");
            this.editDialogVisible = false;
            this.Refresh();
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    Search() {
      this.$http
        .post(
          "/api/articles/makeAnAppointment/search/AppointmentPrint",
          this.queryInfo
        )
        .then(res => {
          if (res.data) {
            console.log("");

            this.tableData = res.data.data;
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  }
};
</script>

<style></style>
