<template>
  <div>
    <!--  -->
    <el-card>
      <div>
        <el-row :gutter="20">
          <el-col :span="7">
            <!--  -->
            <el-input
              placeholder=""
              v-model="queryInfo.query"
              clearable
              @clear="Refresh()"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="Search()"
              ></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <!--  -->
            <el-button type="primary" @click="add = true"></el-button>
          </el-col>
        </el-row>
        <!--  -->
        <el-table :data="tableData" border stripe>
          <el-table-column type="index"></el-table-column>
          <el-table-column label="" prop="Adate"></el-table-column>
          <el-table-column label="" prop="Aname"></el-table-column>
          <el-table-column label="" prop="Aaddress"></el-table-column>
          <el-table-column
            label=""
            prop="Ainfomation"
          ></el-table-column>
          <el-table-column label="" prop="Atime"></el-table-column>
          <el-table-column label="" width="150">
            <template slot-scope="scope">
              <!--  -->
              <el-tooltip
                effect="dark"
                content=""
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="showEditDialog(scope.row.id)"
                ></el-button>
              </el-tooltip>
              <!--  -->
              <el-tooltip
                effect="dark"
                content=""
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="deleteData(scope.row.id)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="PAGINATION.pagenum"
          :page-sizes="[5, 10, 100]"
          :page-size="PAGINATION.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="TotalNumberOfPages"
        >
        </el-pagination>
      </div>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="add"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <span>
        <el-form
          :model="tableDataAdd"
          :rules="rules"
          ref="tableDataAdd"
          label-width="150px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="Aname">
            <el-input v-model="tableDataAdd.Aname"></el-input>
          </el-form-item>
          <el-form-item label="" prop="Aaddress">
            <el-input v-model="tableDataAdd.Aaddress"></el-input>
          </el-form-item>
          <el-form-item label="" prop="Ainfomation">
            <el-input v-model="tableDataAdd.Ainfomation"></el-input>
          </el-form-item>

          <el-form-item label="" prop="Adate">
            <div>
              <el-date-picker
                v-model="tableDataAdd.Adate"
                type="date"
                placeholder=""
              >
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item label="" prop="Atime">
            <el-time-select
              placeholder=""
              v-model="tableDataAdd.Atime"
              :picker-options="{
                start: '08:30',
                step: '00:15',
                end: '18:30'
              }"
            >
            </el-time-select>
          </el-form-item>
        </el-form>
      </span>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="add = false"> </el-button>
        <el-button type="primary" @click="addUser()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <span>
        <el-form
          :model="GtableData"
          :rules="rules"
          ref="GtableData"
          label-width="150px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="Aname">
            <el-input v-model="GtableData.Aname"></el-input>
          </el-form-item>
          <el-form-item label="" prop="Aaddress">
            <el-input v-model="GtableData.Aaddress"></el-input>
          </el-form-item>
          <el-form-item label="" prop="Ainfomation">
            <el-input v-model="GtableData.Ainfomation"></el-input>
          </el-form-item>

          <el-form-item label="" prop="Adate">
            <div>
              <el-date-picker
                v-model="GtableData.Adate"
                type="date"
                placeholder=""
              >
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item label="" prop="Atime">
            <el-time-select
              placeholder=""
              v-model="GtableData.Atime"
              :picker-options="{
                start: '08:30',
                step: '00:15',
                end: '18:30'
              }"
            >
            </el-time-select>
          </el-form-item>
        </el-form>
      </span>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="Change_2()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      tableDataAdd: {
        Adate: "",
        Aname: "",
        Aaddress: "",
        Ainfomation: "",
        Atime: ""
      },

      //
      queryInfo: {
        query: ""
      },

      //
      PAGINATION: {
        //
        pagenum: 1,
        //
        pagesize: 5
      },

      //
      TotalNumberOfPages: 0,

      //
      GtableData: [],
      //----
      value: true,
      //id
      id_id: "",
      //&&
      add: false,
      //&&
      editDialogVisible: false,
      rules: {
        Aname: [
          { required: true, message: "", trigger: "blur" },
          { min: 5, max: 20, message: " 5  20 ", trigger: "blur" }
        ],
        Aaddress: [
          { required: true, message: "", trigger: "blur" }
        ],
        Ainfomation: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.$http
      .get("/api/articles/activity/administration", this.tableData)
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          var obj = {};
          for (var i = 0; i < res.data.data.length; i++) {
            res.data.data[i].Adate = res.data.data[i].Adate.slice(0, 10);
            obj[i] = res.data.data[i];
            this.tableData.push(obj[i]);
            console.log("");
          }
          console.log("");
        } else {
          console.log("");
          this.$notify({
            message: "",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  methods: {
    // ---- 
    handleSizeChange(newSize) {
      this.PAGINATION.pagesize = newSize;
      this.Refresh();
    },
    //  
    handleCurrentChange(newPage) {
      this.PAGINATION.pagenum = newPage;
      this.Refresh();
    },
    //--
    addDialogClosed() {
      this.$refs.tableDataAdd.resetFields();
    },
    //
    Refresh() {
      this.$http
        .get("/api/articles/activity/administration", this.tableData)
        .then(res => {
          console.log(res.data.data.length);
          if (res.data) {
            this.tableData = [];

            var j = this.PAGINATION.pagesize;
            if (
              this.PAGINATION.pagenum * this.PAGINATION.pagesize >
              res.data.data.length
            ) {
              j = res.data.data.length % this.PAGINATION.pagesize;
            }
            for (var i = 0; i < j; i++) {
              this.tableData.push(
                res.data.data[
                  this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
                ]
              );
            }

            this.TotalNumberOfPages = res.data.data.length;

            console.log(this.tableData);
            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //--
    addUser() {
      this.$refs.tableDataAdd.validate(valid => {
        if (!valid) return;
        //-post
        //------
        this.$http
          .post("/api/articles/activity/administration", this.tableDataAdd)
          .then(res => {
            console.log(res);
            if (res.data) {
              console.log(res.data);
              this.add = false;
              this.Refresh();
            } else {
              console.log("");
              this.$notify({
                message: "",
                type: "warning"
              });
            }
          })
          .catch(rej => {
            this.$notify({
              message: rej,
              type: "warning"
            });
          });
      });
    },
    //
    showEditDialog(id) {
      this.editDialogVisible = true;
      //
      this.Change(id);
    },
    //
    deleteData(ID) {
      this.$http
        .delete("/api/articles/activity/administration/" + ID)
        .then(res => {
          if (res.data) {
            console.log("");
            this.Refresh();
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    Change(id) {
      this.$http
        .get("/api/articles/activity/administration")
        .then(res => {
          if (res.data) {
            for (var i = 0; i < res.data.data.length; i++) {
              if (res.data.data[i].id == id) {
                this.GtableData = res.data.data[i];
                this.id_id = id;
              }
            }
            console.log(this.GtableData);
            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    Change_2() {
      this.$http
        .post(
          "/api/articles/activity/administration/" + this.id_id,
          this.GtableData
        )
        .then(res => {
          if (res.data) {
            console.log("");
            this.editDialogVisible = false;
            this.Refresh();
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    Search() {
      this.$http
        .post("/api/articles/activity/search/administration", this.queryInfo)
        .then(res => {
          if (res.data) {
            console.log("");

            this.tableData = res.data.data;
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  }
};
</script>

<style></style>
