<template>
  <el-form ref="form" :model="form" :rules="rules" label-width="100px">
    <el-form-item label="" prop="Pregion">
      <el-select v-model="form.Pregion" placeholder="">
        <el-option label="" value="A"></el-option>
        <el-option label="" value="B"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" prop="Pdate">
      <el-col :span="11">
        <el-form-item prop="Pdate">
          <el-date-picker
            v-model="form.Pdate"
            type="datetime"
            placeholder=""
          >
          </el-date-picker>
        </el-form-item>
      </el-col>
    </el-form-item>
    <el-form-item label="" auto-upload="false">
      <el-switch v-model="form.Pdelivery"> </el-switch>
      <el-upload
        class="upload-demo"
        :action="domain"
        :http-request="upqiniu"
        :show-file-list="false"
        :before-upload="beforeUpload"
      >
        <el-button size="small" type="primary"></el-button>
      </el-upload>
    </el-form-item>
    <el-form-item type="primary" label="" placeholder="">
      <el-input type="textarea" v-model="form.Premark"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('form')"></el-button>
      <el-button></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: {},
      //
      domain: "http://upload.qiniup.com/",
      //
      qiniuaddr: "qiniu.whitelion233.top",
      form: {
        //
        Pregion: "",
        Pdate: "",
        Pdelivery: false,
        Premark: "",
        Purl: ""
      },
      rules: {
        //
        Pregion: [
          { required: true, message: "", trigger: "change" }
        ],
        Pdate: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      //
      this.$http
        .post("/api/appointmentprint", this.form)
        .then(res => {
          if (res.data) {
            this.$notify({
              title: "",
              message: "",
              type: "success"
            });
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //**********************************************************************************
    upqiniu(req) {
      const config = {
        headers: { "Content-Type": "multipart/form-data" }
      };
      //
      let uploadImgName = req.file.name.substring(
        0,
        req.file.name.indexOf(".")
      );
      // 
      const keyname = "cwh-print_" + uploadImgName + "." + "docx";
      // token,proxyTable
      axios.get("/api/token").then(res => {
        const formdata = new FormData();
        formdata.append("file", req.file);
        formdata.append("token", res.data);
        formdata.append("key", keyname);
        // 
        axios.post(this.domain, formdata, config).then(res => {
          this.form.Purl = "http://" + this.qiniuaddr + "/" + res.data.key;
        });
      });
      this.$notify({
        title: "",
        message: "",
        type: "success"
      });
    },
    // 
    beforeUpload(file) {
      const isJPG =
        file.type ===
        "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
      if (!isJPG) {
        this.$message.error("docx!");
      }
    }
  }
};
</script>
