<template>
  <div>
    <!--  -->
    <el-card>
      <div>
        <el-card shadow="always"> :{{ TotalNumber }} </el-card>
        <!--
        <el-row :gutter="20">
          <el-col :span="8">
            <el-card shadow="always"> :{{ TotalNumber }} </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="always"> :{{ b }} </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="always"> {{ c }} </el-card>
          </el-col>
        </el-row>
        -->
      </div>

      <!-- EChartsDom -->
      <div id="main" style="width: 100%;height:560px;"></div>
    </el-card>
  </div>
</template>

<script>
//echarts
import echarts from "echarts";

export default {
  data() {
    return {
      TotalNumber: "",
      b: "10",
      c: "600"
    };
  },
  created() {
    this.$http
      .get("/api/articles/activity/administration")
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          this.TotalNumber = res.data.data.length;

          console.log("");
        } else {
          console.log("");
          this.$notify({
            message: "",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  async mounted() {
    // domecharts
    var myChart = echarts.init(document.getElementById("main"));

    // 
    const { data: res } = await this.$http.get("/api/articles/activity/core/T");

    // 
    myChart.setOption(res.data);
  }
};
</script>

<style></style>
