<template>
  <div>
    <!--  -->
    <el-card>
      <div>
        <el-row :gutter="0">
          <el-col :span="8">
            <!--  -->
            <el-input
              placeholder=""
              v-model="queryInfo.query"
              clearable
              @clear="Refresh()"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="Search()"
              ></el-button>
            </el-input>
          </el-col>
          <el-col :span="16"> </el-col>
        </el-row>
        <!--  -->
        <el-table :data="tableData" border stripe>
          <el-table-column type="index"></el-table-column>
          <el-table-column label="" prop="Lstate"></el-table-column>
          <el-table-column label="" prop="Ldate"></el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-image
                style="width: 100px; height: 100px"
                :src="tableData[scope.$index].Lurl"
                :preview-src-list="tableData[scope.$index].Lurl"
              >
              </el-image>
            </template>
          </el-table-column>
          <el-table-column label="" prop="Lname"></el-table-column>
          <el-table-column
            label=""
            prop="Linfomation"
          ></el-table-column>
          <el-table-column label="" width="150">
            <template slot-scope="scope">
              <!--  -->
              <el-tooltip
                effect="dark"
                content=""
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="showEditDialog(scope.row.id)"
                ></el-button>
              </el-tooltip>
              <!--  -->
              <el-tooltip
                effect="dark"
                content=""
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="deleteData(scope.row.id)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="PAGINATION.pagenum"
          :page-sizes="[5, 10, 100]"
          :page-size="PAGINATION.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="TotalNumberOfPages"
        >
        </el-pagination>
      </div>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <span>
        <el-form
          :model="GtableData"
          :rules="rules"
          ref="GtableData"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="Lname">
            <el-input
              v-model="GtableData.Lname"
              maxlength="10"
              minlength="3"
              type="text"
              show-word-limit
            ></el-input>
          </el-form-item>
          <el-form-item label="" required>
            <el-col :span="11">
              <el-form-item prop="Ldate">
                <el-date-picker
                  type="date"
                  placeholder=""
                  v-model="GtableData.Ldate"
                  style="width: 100%;"
                ></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="Lcontact">
            <el-input type="text" v-model="GtableData.Lcontact"></el-input>
          </el-form-item>
          <el-form-item label="" prop="Linfomation">
            <el-input
              type="textarea"
              v-model="GtableData.Linfomation"
            ></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="Change_2()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      //
      ruleForm: {
        Lstate: false,
        Ldate: "",
        Lname: "",
        Linfomation: "",
        Lcontact: "",
        Lurl: ""
      },

      //
      queryInfo: {
        query: ""
      },

      //
      PAGINATION: {
        //
        pagenum: 1,
        //
        pagesize: 5
      },

      //
      TotalNumberOfPages: 0,

      //
      GtableData: [],
      //----
      value: true,
      //id
      id_id: "",
      //&&
      editDialogVisible: false,

      rules: {
        Lname: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  10 ", trigger: "blur" }
        ],
        Ldate: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        Lcontact: [
          { required: true, message: "", trigger: "blur" }
        ],
        Linfomation: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.$http
      .get("/api/articles/findLostItems/administration", this.tableData)
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          this.TotalNumberOfPages = res.data.data.length;

          this.tableData = [];
          for (var i = 0; i < res.data.data.length; i++) {
            if (res.data.data[i].Lstate == 1) {
              res.data.data[i].Ldate = "";
            } else res.data.data[i].Ldate = "";
            res.data.data[i].Ldate = res.data.data[i].Ldate.slice(0, 10);
            this.tableData.push(
              res.data.data[
                this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
              ]
            );
          }
        } else {
          console.log("");
          this.$notify({
            message: "",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  methods: {
    // ---- 
    handleSizeChange(newSize) {
      this.PAGINATION.pagesize = newSize;
      this.Refresh();
    },
    //  
    handleCurrentChange(newPage) {
      this.PAGINATION.pagenum = newPage;
      this.Refresh();
    },
    // switch 
    userStateChanged() {},
    //
    showEditDialog(id) {
      this.editDialogVisible = true;
      //
      this.Change(id);
    },
    //
    Refresh() {
      this.$http
        .get("/api/articles/findLostItems/administration", this.tableData)
        .then(res => {
          console.log(res.data.data.length);
          if (res.data) {
            this.tableData = [];

            for (var i = 0; i < res.data.data.length; i++) {
              res.data.data[i].Ldate = res.data.data[i].Ldate.slice(0, 10);
              this.tableData.push(
                res.data.data[
                  this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
                ]
              );
            }

            this.TotalNumberOfPages = res.data.data.length;

            console.log(this.tableData);
            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    deleteData(ID) {
      this.$http
        .delete("/api/articles/findLostItems/administration/" + ID)
        .then(res => {
          if (res.data) {
            console.log("");
            this.Refresh();
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    Change(id) {
      this.$http
        .get("/api/articles/findLostItems/administration")
        .then(res => {
          if (res.data) {
            for (var i = 0; i < res.data.data.length; i++) {
              if (res.data.data[i].id == id) {
                this.GtableData = res.data.data[i];
                this.id_id = id;
              }
            }
            console.log(this.GtableData);
            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    Change_2() {
      this.$http
        .post(
          "/api/articles/findLostItems/administration/" + this.id_id,
          this.GtableData
        )
        .then(res => {
          if (res.data) {
            console.log("");
            this.editDialogVisible = false;
            this.Refresh();
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    Search() {
      this.$http
        .post(
          "/api/articles/findLostItems/search/administration",
          this.queryInfo
        )
        .then(res => {
          if (res.data) {
            console.log("");

            this.tableData = res.data.data;
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  }
};
</script>

<style></style>
