<template>
  <el-container style="height:100%; border: 1px solid #eee">
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-image class="LogoAP" :src="src"></el-image>
      <el-menu :default-openeds="['1', '3']">
        <el-menu-item index="1">
          <i class="el-icon-s-order"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="2" @click="onloading()">
          <i class="el-icon-s-home"></i>
          <span slot="title"></span>
        </el-menu-item>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <span></span>
      </el-header>

      <el-main>
        <el-button type="primary" @click="refresh()"></el-button>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="id" label="" sortable width="180">
          </el-table-column>
          <el-table-column prop="Cdate" label="" sortable width="180">
          </el-table-column>
          <el-table-column prop="Cname" label="" sortable width="180">
          </el-table-column>
          <el-table-column
            prop="Caddress"
            label=""
            :formatter="formatter"
          >
          </el-table-column>
          <el-table-column
            prop="Cinfomation"
            label=""
            :formatter="formatter"
          >
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="success"
                @click="Onclick(tableData[scope.$index].Csrc)"
                ></el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-main>
    </el-container>
  </el-container>
</template>
<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}
.el-main {
  height: 100%;
}
.el-aside {
  color: #333;
  height: 100%;
}
a {
  text-decoration: none;
}
.LogoAP {
  width: 135px;
  height: auto;
  margin-left: 25px;
  margin-top: 0px;
}
.router-link-active {
  text-decoration: none;
}
</style>

<script>
export default {
  data() {
    return {
      src:
        "http://img.whitelion233.top/images/2020/03/25/logoa104e2210c1ef437.png",
      tableData: []
    };
  },
  methods: {
    onloading() {
      //
      const loading = this.$loading({
        lock: true,
        text: "",
        spinner: "el-icon-loading"
      });
      setTimeout(() => {
        loading.close();
        this.$router.push("/");
      }, 1000);
    },
    Onclick(Csrc) {
      //
      window.open(Csrc);
    },
    refresh() {
      //
      this.$http
        .post("/api/course", this.tableData)
        .then(res => {
          if (res.data) {
            var obj = {};
            for (var i = 0; i < res.data.data.length; i++) {
              res.data.data[i].Cdate = res.data.data[i].Cdate.slice(0, 10);
              obj[i] = res.data.data[i];
              if (this.tableData[i].id != res.data.data[i].id) {
                this.tableData.push(obj[i]);
                console.log("");
              } else console.log("");
            }

            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  },
  created: function() {
    //
    if (localStorage.state >= 0) {
      //
      this.$http
        .post("/api/course", this.tableData)
        .then(res => {
          if (res.data) {
            var obj = {};
            for (var i = 0; i < res.data.data.length; i++) {
              if (this.tableData[i] != res.data.data[i]) {
                res.data.data[i].Cdate = res.data.data[i].Cdate.slice(0, 10);
                obj[i] = res.data.data[i];
                this.tableData.push(obj[i]);
              }
            }
            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    } else {
      //
      this.$alert("", "", {
        confirmButtonText: "",
        type: "warning",
        customClass: "alert",
        callback: action => {
          this.$router.push("/login");
        }
      });
    }
  }
};
</script>
