<template>
  <el-form
    :model="ruleForm"
    :rules="rules"
    ref="ruleForm"
    label-width="100px"
    class="demo-ruleForm"
  >
    <el-form-item label="" prop="Lname">
      <el-input
        v-model="ruleForm.Lname"
        maxlength="10"
        minlength="3"
        type="text"
        show-word-limit
      ></el-input>
    </el-form-item>
    <el-form-item label="" required>
      <el-col :span="11">
        <el-form-item prop="Ldate">
          <el-date-picker
            type="date"
            placeholder=""
            v-model="ruleForm.Ldate"
            style="width: 100%;"
          ></el-date-picker>
        </el-form-item>
      </el-col>
    </el-form-item>
    <el-form-item label="" prop="Lcontact">
      <el-input type="text" v-model="ruleForm.Lcontact"></el-input>
    </el-form-item>
    <el-form-item label="" prop="Linfomation">
      <el-input type="textarea" v-model="ruleForm.Linfomation"></el-input>
    </el-form-item>
    <el-form-item label="" prop="Limage">
      <el-upload
        class="upload-demo"
        drag
        :action="domain"
        :http-request="upqiniu"
        :show-file-list="false"
        :before-upload="beforeUpload"
        multiple
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">
          jpg/png5M
        </div>
      </el-upload>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')"
        ></el-button
      >
      <el-button @click="resetForm('ruleForm')"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: {},
      //
      domain: "http://upload.qiniup.com/",
      //
      qiniuaddr: "qiniu.whitelion233.top",
      ruleForm: {
        Lname: "",
        Ldate: "",
        Lcontact: "",
        Linfomation: "",
        Lurl: "",
        Lid: ""
      },
      rules: {
        Lname: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  10 ", trigger: "blur" }
        ],
        Ldate: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        Lcontact: [
          { required: true, message: "", trigger: "blur" }
        ],
        Linfomation: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      //
      this.ruleForm.Lid = localStorage.ID;
      this.$http
        .post("/api/lostadd", this.ruleForm)
        .then(res => {
          if (res.data) {
            this.$notify({
              title: "",
              message: "",
              type: "success"
            });
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    resetForm(formName) {
      //
      this.$refs[formName].resetFields();
    },

    //**********************************************************************************
    upqiniu(req) {
      const config = {
        headers: { "Content-Type": "multipart/form-data" }
      };
      let filetype = "";
      if (req.file.type === "image/png") {
        filetype = "png";
      } else {
        filetype = "jpg";
      }
      //
      let uploadImgName = req.file.name.substring(
        0,
        req.file.name.indexOf(".")
      );
      // 
      const keyname =
        "cwh-imglist_" + uploadImgName + "_" + new Date().getTime();
      "." + filetype;
      // token,proxyTable
      axios.get("/api/token").then(res => {
        const formdata = new FormData();
        formdata.append("file", req.file);
        formdata.append("token", res.data);
        formdata.append("key", keyname);
        // 
        axios.post(this.domain, formdata, config).then(res => {
          this.ruleForm.Lurl = "http://" + this.qiniuaddr + "/" + res.data.key;
        });
      });
      this.$notify({
        title: "",
        message: "",
        type: "success"
      });
    },
    // 
    beforeUpload(file) {
      const isJPG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 5;
      if (!isJPG) {
        this.$message.error(" JPGpng !");
      }
      if (!isLt2M) {
        this.$message.error(" 5MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>
