<template>
  <div>
    <!--  -->
    <el-card>
      <div>
        <el-row :gutter="20">
          <el-col :span="7">
            <!--  -->
            <el-input
              placeholder=""
              v-model="queryInfo.query"
              clearable
              @clear="Refresh()"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="Search()"
              ></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <!--  -->
            <el-button type="primary" @click="add = true"></el-button>
          </el-col>
        </el-row>
        <!--  -->
        <el-table :data="tableData" border stripe>
          <el-table-column type="index"></el-table-column>
          <el-table-column label="" prop="username"></el-table-column>
          <el-table-column label="" prop="password"></el-table-column>
          <el-table-column label="" prop="ID"></el-table-column>
          <el-table-column label="" prop="Email"></el-table-column>
          <el-table-column label="" width="150">
            <template slot-scope="scope">
              <!--  -->
              <el-tooltip
                effect="dark"
                content=""
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="showEditDialog(scope.row.ID)"
                ></el-button>
              </el-tooltip>
              <!--  -->
              <el-tooltip
                effect="dark"
                content=""
                placement="right-end"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="deleteData(scope.row.ID)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="PAGINATION.pagenum"
          :page-sizes="[5, 10, 100]"
          :page-size="PAGINATION.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="TotalNumberOfPages"
        >
        </el-pagination>
      </div>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="add"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <span>
        <el-form
          :model="userlistAdd"
          :rules="rules"
          ref="userlistAdd"
          label-width="80px"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="userlistAdd.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="userlistAdd.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="ID">
            <el-input v-model="userlistAdd.ID"></el-input>
          </el-form-item>
          <el-form-item label="" prop="Email">
            <el-input v-model="userlistAdd.Email"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="add = false"> </el-button>
        <el-button type="primary" @click="addUser()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <span>
        <el-form
          :model="GtableData"
          :rules="rules"
          ref="GtableData"
          label-width="80px"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="GtableData.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="GtableData.password"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="GtableData.ID" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="Email">
            <el-input v-model="GtableData.Email"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="Change_2()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    //
    var checkEmail = (rule, value, cd) => {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;

      if (regEmail.test(value)) {
        //
        return cd();
      }

      cd(new Error(""));
    };

    return {
      tableData: [],

      //
      userlistAdd: {
        username: "",
        password: "",
        ID: "",
        Email: ""
      },

      //
      queryInfo: {
        query: ""
      },
      //
      TotalNumberOfPages: 0,

      //
      PAGINATION: {
        //
        pagenum: 1,
        //
        pagesize: 5
      },

      //
      GtableData: [],
      //----
      value: true,
      //id
      id_id: "",

      //&&
      add: false,
      //&&
      editDialogVisible: false,
      //
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 12, message: " 2  12 ", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 15, message: " 6  15 ", trigger: "blur" }
        ],
        ID: [
          { required: true, message: "", trigger: "blur" },
          { min: 10, max: 10, message: "", trigger: "blur" }
        ],
        Email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.$http
      .get("/api/articles/accountManagement/administration", this.tableData)
      .then(res => {
        console.log(res.data.data.length);
        if (res.data) {
          this.TotalNumberOfPages = res.data.data.length;

          this.tableData = [];

          var j = this.PAGINATION.pagesize;
          if (
            this.PAGINATION.pagenum * this.PAGINATION.pagesize >
            res.data.data.length
          ) {
            j = res.data.data.length % this.PAGINATION.pagesize;
          }
          for (var i = 0; i < j; i++) {
            this.tableData.push(
              res.data.data[
                this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
              ]
            );
          }

          console.log(this.tableData);
          console.log("");
        } else {
          console.log("");
          this.$notify({
            message: "",
            type: "warning"
          });
        }
      })
      .catch(rej => {
        this.$notify({
          message: rej,
          type: "warning"
        });
      });
  },
  methods: {
    // ---- 
    handleSizeChange(newSize) {
      this.PAGINATION.pagesize = newSize;
      this.Refresh();
    },
    //  
    handleCurrentChange(newPage) {
      this.PAGINATION.pagenum = newPage;
      this.Refresh();
    },
    // switch 
    userStateChanged() {},
    //
    addDialogClosed() {
      this.$refs.userlistAdd.resetFields();
    },
    //
    Refresh() {
      this.$http
        .get("/api/articles/accountManagement/administration", this.tableData)
        .then(res => {
          console.log(res.data.data.length);
          if (res.data) {
            this.tableData = [];

            var j = this.PAGINATION.pagesize;
            if (
              this.PAGINATION.pagenum * this.PAGINATION.pagesize >
              res.data.data.length
            ) {
              j = res.data.data.length % this.PAGINATION.pagesize;
            }
            for (var i = 0; i < j; i++) {
              this.tableData.push(
                res.data.data[
                  this.PAGINATION.pagesize * (this.PAGINATION.pagenum - 1) + i
                ]
              );
            }

            this.TotalNumberOfPages = res.data.data.length;

            console.log(this.tableData);
            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //--
    addUser() {
      this.$refs.userlistAdd.validate(valid => {
        if (!valid) return;
        //-post
        this.$http
          .post(
            "/api/articles/accountManagement/administration",
            this.userlistAdd
          )
          .then(res => {
            console.log(res);
            if (res.data) {
              console.log(res.data);
              this.add = false;
              this.Refresh();
            } else {
              console.log("");
              this.$notify({
                message: "",
                type: "warning"
              });
            }
          })
          .catch(rej => {
            this.$notify({
              message: rej,
              type: "warning"
            });
          });
      });
    },
    //
    showEditDialog(id) {
      this.editDialogVisible = true;
      //
      this.Change(id);
    },
    //
    deleteData(ID) {
      this.$http
        .delete("/api/articles/accountManagement/administration/" + ID)
        .then(res => {
          if (res.data) {
            console.log("");
            this.Refresh();
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    Change(id) {
      this.$http
        .get("/api/articles/accountManagement/administration")
        .then(res => {
          if (res.data) {
            for (var i = 0; i < res.data.data.length; i++) {
              if (res.data.data[i].ID == id) {
                this.GtableData = res.data.data[i];
                this.id_id = id;
              }
            }
            console.log(this.GtableData);
            console.log("");
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    Change_2() {
      this.$http
        .post(
          "/api/articles/accountManagement/administration/" + this.id_id,
          this.GtableData
        )
        .then(res => {
          if (res.data) {
            console.log("");
            this.editDialogVisible = false;
            this.Refresh();
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    //
    Search() {
      this.$http
        .post(
          "/api/articles/accountManagement/search/administration",
          this.queryInfo
        )
        .then(res => {
          if (res.data) {
            console.log("");

            this.tableData = res.data.data;
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    }
  }
};
</script>

<style></style>
