<template>
  <el-form
    :model="ruleForm"
    :rules="rules"
    ref="ruleForm"
    label-width="100px"
    class="demo-ruleForm"
  >
    <el-form-item label="" prop="Sname">
      <el-input v-model="ruleForm.Sname"></el-input>
    </el-form-item>
    <el-form-item label="" prop="Sstudentname">
      <el-input v-model="ruleForm.Sstudentname"></el-input>
    </el-form-item>
    <el-form-item label="" prop="Snumber">
      <el-input v-model="ruleForm.Snumber"></el-input>
    </el-form-item>
    <el-form-item label="" prop="Sclass">
      <el-input v-model="ruleForm.Sclass"></el-input>
    </el-form-item>
    <el-form-item label="" prop="Sregion">
      <el-select v-model="ruleForm.Sregion" placeholder="">
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=" "></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" required>
      <el-col :span="11">
        <el-form-item prop="Sdate1">
          <el-date-picker
            v-model="ruleForm.Sdate1"
            type="datetime"
            placeholder=""
          >
          </el-date-picker>
        </el-form-item>
      </el-col>
    </el-form-item>
    <el-form-item label="" prop="Sresource">
      <el-radio-group v-model="ruleForm.Sresource">
        <el-radio label=""></el-radio>
        <el-radio label=""></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="" prop="Sdesc">
      <el-input type="textarea" v-model="ruleForm.Sdesc"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm()"></el-button>
      <el-button @click="resetForm('ruleForm')"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        Sname: "",
        Sstudentname: "",
        Snumber: "",
        Sclass: "",
        Sregion: "",
        Sdate1: "",
        Sresource: "",
        Sdesc: ""
      },
      rules: {
        //
        Sname: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        Sstudentname: [
          { required: true, message: "", trigger: "blur" }
        ],
        Sclass: [{ required: true, message: "", trigger: "blur" }],
        Snumber: [
          { required: true, message: "", trigger: "blur" }
        ],
        Sregion: [
          { required: true, message: "", trigger: "change" }
        ],
        Sdate1: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        Sresource: [
          { required: true, message: "", trigger: "change" }
        ],
        Sdesc: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm() {
      //
      this.$http
        .post("/api/appointmentsport", this.ruleForm)
        .then(res => {
          if (res.data) {
            this.$notify({
              title: "",
              message: "",
              type: "success"
            });
          } else {
            console.log("");
            this.$notify({
              message: "",
              type: "warning"
            });
          }
        })
        .catch(rej => {
          this.$notify({
            message: rej,
            type: "warning"
          });
        });
    },
    resetForm(formName) {
      //
      this.$refs[formName].resetFields();
    }
  }
};
</script>
